<template>
  <div style="margin-top: 20px">
    <ct-section-title class="content-info-title" :title="$t('order.fucName')" />
    <el-table ref="table" v-loading="loading" :data="tableData" :border="true">
      <el-table-column
        prop="objName"
        :label="$t('order.objName')"
        show-overflow-tooltip
      />
      <el-table-column
        prop="objType"
        :label="$t('order.objType')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="operType"
        :label="$t('order.operType')"
        :formatter="formateOperType"
      >
      </el-table-column>
      <el-table-column
        prop="originValue"
        :label="$t('order.originValue')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="newValue"
        :label="$t('order.newValue')"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {}
  },

  methods: {
    itemInfoFormatter(obj) {
      const des = obj.formatter() ?? '-'
      return des
    },
    formateOperType(row, col, val) {
      switch (val) {
        case '1000':
          return this.$t('order.operTypeAdd')
        case '1100':
          return this.$t('order.operTypeDelete')
        case '1200':
          return this.$t('order.operTypeChange')
        case '1300':
          return this.$t('order.operTypeRemain')
      }
    },
  },
}
</script>
