<template>
  <div class="container">
    <div class="container-content">
      <div class="container-top">
        <div class="container-title-container">
          <ct-section-title :title="orderTitle" />
          <div class="container-title-container-right">{{ row.statusCdName }}</div>
        </div>
        <el-row :gutter="20">
          <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
            <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
          </el-col>
        </el-row>
      </div>
      <div class="container-bottom">
        <div class="container-form">
          <OfferInfo v-if="bizOfferInstDtos.length > 0" :table-data="bizOfferInstDtos" />
          <ProdInfo v-if="orderProdDtos.length > 0" :table-data="orderProdDtos" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import OfferInfo from './OfferInfo.vue'
import ProdInfo from './ProdInfo.vue'
export default {
  components: {
    OfferInfo,
    ProdInfo,
  },
  props: {},
  data() {
    return {
      row: JSON.parse(this.$route.query.row),
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      detailInfo: {},
      bizOfferInstDtos: [],
      orderProdDtos: [],
    }
  },
  computed: {
    infoList() {
      return [
        {
          label: 'ICCID',
          prop: 'iccid',
          formatter: () => this.row.iccid,
        },
        {
          label: 'IMSI',
          prop: 'imsi',
          formatter: () => this.row.imsi,
        },
        {
          label: this.$t('order.accNum'),
          formatter: () => this.row.accNum,
        },
        {
          label: this.$t('order.serviceTime'),
          formatter: () => this.row.acceptDate,
        },
        {
          label: this.$t('order.cust'),
          formatter: () => this.row.useCustName,
        },
        {
          label: this.$t('order.source'),
          formatter: () => this.row.cmpOrderSourceName,
        },
        {
          label: this.$t('order.account'),
          formatter: () => this.row.cmpCreateStaffName,
        },
      ]
    },
    orderTitle() {
      return this.$t('order.orderCodeTitle') + this.row.custOrderNbr
    },
  },
  mounted() {
    this.queryDetail()
  },
  methods: {
    async queryDetail() {
      const { custOrderId, msisdn } = this.row
      const res = await fetch(
        '/order/qryOrderDetail',
        {
          custOrderId,
          msisdn,
        },
        { method: 'post' }
      )
      this.detailInfo = res
      this.orderProdDtos = this.detailInfo.orderProdDtos || []
      this.bizOfferInstDtos = this.detailInfo.bizOfferInstDtos || []
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter() ?? '-'
      return des
    },
  },
}
</script>
